<template>
  <div id="login">
    <b-form class="form-signin" @submit.prevent="submitLogin">
      <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>
        <b-form-input type="email" id="inputEmail" placeholder="Email address" required autofocus>
        </b-form-input>
        <b-form-input type="password" id="inputPassword" placeholder="Password" required>
        </b-form-input>
      <b-button type="submit" variant="primary" class="btn-lg btn-block">Sign in</b-button>
    </b-form>
  </div>
</template>

<script>
export default {
  name: 'Login',
  methods: {
    submitLogin: function() {
      this.$store.dispatch('authData/login', {username: 'admin', password: 'admin' })
    }
  }
}
</script>

<style scoped>
  #login {
    height: 100%;
  }

  #login {
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #f5f5f5;
  }

  .form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: auto;
  }
  .form-signin .checkbox {
    font-weight: 400;
  }
  .form-signin .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
  }
  .form-signin .form-control:focus {
    z-index: 2;
  }
  .form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
</style>



